import React, { memo } from 'react';
import { bool, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-input-message';
const CSS_STATE = 'vds-state';

const InputMessageError = memo(({
  ariaAtomic,
  ariaLive,
  children,
  className,
  role,
  show,
  ...remainingProps
}) => (
  <div
    aria-atomic={ariaAtomic}
    aria-live={ariaLive}
    className={classnames(CSS_PREFIX, `${CSS_STATE}--error`, {
      [`${CSS_STATE}--hidden`]: !show
    }, className)}
    role={role}
    {...remainingProps}
  >
    {children}
  </div>
));

InputMessageError.propTypes = {
  /**
   * Aria Atomic
   */
  ariaAtomic: oneOf(['true', 'false']),

  /**
   * Aria Live
   */
  ariaLive: oneOf(['assertive', 'polite', 'off']),

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Invalid
   */
  // invalid: bool,

  /**
   * Role
   */
  role: string,

  /**
   * Show
   */
  show: bool
};

InputMessageError.defaultProps = {
  ariaAtomic: 'true',
  ariaLive: 'assertive',
  role: 'alert'
};

InputMessageError.displayName = 'InputMessageError';

export default InputMessageError;
