import React, { memo } from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-input-message';
const CSS_STATE = 'vds-state';

const InputMessageHelp = memo(({
  children,
  className,
  invalid,
  show,
  ...remainingProps
}) => (
  <span
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--hidden`]: !show,
      [`${CSS_STATE}--error`]: Boolean(invalid)
    }, className)}
    {...remainingProps}
  >
    {children}
  </span>
));

InputMessageHelp.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Invalid
   */
  invalid: bool,

  /**
   * Show
   */
  show: bool
};

InputMessageHelp.defaultProps = {
  // ariaAtomic: 'true',
  // role: 'alert'
};

InputMessageHelp.displayName = 'InputMessageHelp';

export default InputMessageHelp;
